.color_bg {
  /* Set rules to fill background */
  min-height: 100%;
  min-width: 1024px;

  /* Set up proportionate scaling */
  width: 100%;
  height: auto;

  /* Set up positioning */
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background: -webkit-gradient(linear, left top, left bottom, from(#dbeee6), to(#fff));
  background: -webkit-linear-gradient(#dbeee6, #fff);
  background: -o-linear-gradient(#dbeee6, #fff);
  background: linear-gradient(#dbeee6, #fff);
}


